
import { useEffect, useState } from 'react';
import $ from 'jquery';


function Register(){
  let [region] = useState(['', '', '', '', '', '', '', '', '', '', '', '', '', '', '']);
  let [obj, setObj] =  useState();

  useEffect(() => {
    let getItem = localStorage.getItem('place');
    getItem = JSON.parse(getItem);
    if(Object.keys(getItem).length != 0 && obj != null){

      if(getItem[obj.day]){
        getItem[obj.day].push(obj);
      } else {
        getItem[obj.day] = [obj];
      }

      localStorage.setItem('place', JSON.stringify(getItem));

    } else if(obj != null) {
      getItem[obj.day] = [obj];
      localStorage.setItem('place', JSON.stringify(getItem));
    }
  }, [obj]);

  return(
    <div className="register-container">
      <h3>Registering<br/> A <br/> Place</h3>
      <div className="register-main-container">
        <form action="#">
          {/* <label htmlFor="placeLocation">
            Place's Location
            <input type="text" id="placeLocation"></input>
          </label> */}
          <label htmlFor="placeLocation"> Place's Location
            <select id="placeLocation" defaultValue={''}>
              {
                region.map((value, i) => {
                  return(
                    <option key={i} value={value}>{value}</option>
                  )
                })
              }
            </select>
          </label>
          <label htmlFor="placeName">
            Place Name
            <input type="text" id="placeName"></input>
          </label>
          <label htmlFor="placeCategory">
            Place Category
            <input type="text" id="placeCategory"></input>
          </label>
          <label htmlFor='days'>
            What day are you going?
            <input type='number' id="days"></input>
          </label>
          <button className="register-button" onClick={() => {
            let placeLocation = $('#placeLocation').val();
            let placeName = $('#placeName').val();
            let placeCategory = $('#placeCategory').val();
            let days = $('#days').val();
            
            // input 
            // if(!/\S/.test(placeLocation)){
            //   window.alert("input the Place's Location");
            // } 
            if(!/\S/.test(placeName)){
              window.alert("input the Place Name");
            }
            if(!/\S/.test(placeCategory)){
              window.alert("input the Place Category");
            } 
            if(!/[0-9]/.test(days)){
              window.alert("input the days");
            } 

            // LocalStorage
            if(/\S/.test(placeLocation) && /\S/.test(placeName) && /\S/.test(placeCategory) && /[0-9]/.test(days)){
              let newObj = {
                location : placeLocation,
                locationName : placeName,
                locationCategory : placeCategory,
                day : days,
                time : ['00', '00', '00', '00']
              }
              setObj(newObj);

              window.alert("Registered! ")
            }
          }}>Register</button>
        </form>
      </div>
    </div>
  )
}





export {Register}